import styled, { css } from 'styled-components'
import theme from '../theme'

const smallStyles = `
  font-size: 0.75em;
`

const StyledButton = styled.a`
  ${props => css`
    background-color: ${theme.colors[props.bgColor]};
    color: ${theme.colors[props.fontColor]};
    &:hover {
      background-color: ${theme.colors[`${props.bgColor}Light`]};
    }
  `}
  padding: 0.5em 1em;
  text-decoration: none;
  ${props => {
    if (props.small) {
      return css`${smallStyles}`
    }
  }}
`

StyledButton.defaultProps = {
  bgColor: 'blue',
  fontColor: 'white',
}

const AnchorButton = (props) => {
  return (
    <StyledButton {...props}>
      {props.children}
    </StyledButton>
  )
}

export default AnchorButton
